}

.Home {
  width: 100vw;
  height: 100vh;
  padding: 0%;
  background-color: #ececee;
  overflow: hidden;
}

.Home__header-main {
  font-size: 60px;
  font-weight: 750;
  color: #412B73;
  padding-top: 2%;
}

.Home__header-sub {
  color: #373A56;
  font-size: 24px;
  margin-top: 2%;
}

.Home__body {
  color: #A6A6B4;
  font-size: 15px;
  margin: 0%;
  padding: 0;
  margin-top: 0.5%;
}

.Home__button-subscribe {
  background-color: #FBBC89;
  color: white;
  border: none;
  border-radius: 40px;
  font-size: 15px;
  padding-left: 6%;
  padding-right: 6%;
  padding-top: 2%;
  padding-bottom: 2%;
  margin-top: 4%;
  font-weight: 750;
  letter-spacing: 2px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
  cursor: pointer;
}

.Home__button-subscribe:hover {
  color: rgba(255, 255, 255, 0.7);
  background-color: rgba(254, 145, 77, 0.7);
}

.Home__image {
  padding-top: 5%;
  width: 90%;
}

.Home__right {
  padding-top: 10%;
}

.Home__icon {
  z-index: 10000;
  font-size: 22px;
  color: black;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
}

.Home__icon:hover {
  color: rgba(55, 58, 86, 0.4);
}

.Home__social-container {
  margin-top: 4%;
}

@media only screen and (max-width: 600px) {
  .Home__header-mini {
    font-size: 20px;
  }

  .Home__header-main {
    font-size: 80px;
  }

  .Home__header-subMain {
    font-size: 20px;
  }

  .Home__button-subscribe {
    margin-top: 4%;
  }

  .Home__mobile-container {
    width: 100%;
    text-align: left;
  }

  .Home__image {
    margin: auto;
    width: 50%;
    margin-left: 25%;
  }

  .faqTitle{
    font-size: 40px;
    font-weight: 750;
    color: #373A56;
  }

  /* /* .primaryFaq{
    font-size: 30px;
  } */
  .subfaqtitle{
    font-size: 30px;
  }
  .title{
    font-size: 30px;
  }
}

