  padding: 10%;
  padding-top: 1%;
}

.Faq__more {
  font-size: 14px;
  font-weight: 750;
  color: #FBBC89;
  padding: 1.2%;
  border-radius: 40px;
  border: 1px solid #FBBC89;
  display: inline-block;
  margin-top: 2%;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
}

.Faq__more:hover {
  color: rgba(251, 188, 137, 0.5);
  border-color: rgba(251, 188, 137, 0.5);
}

.Faq__more_container {
  width: 100%;
  text-align: center;
}

.Faq__dots {
  position: absolute;
  left: 6%;
}

@-webkit-keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 15px;
  }
}

@keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 15px;
  }
}
